




















































































































































































































































































	import allApi from '../../common/allApi.js';
	import http from '../../common/http.js';
	let tools = require('../../common/tools.js');

	import {
		mapState
	} from 'vuex';

	export default {
		data() {
			return {
				isUnloack: 0, // 是否解锁 0 默认不予显示 优化解锁or未解锁体验 1 确定未解锁 2 确定已解锁 
				mapId: '',
				payDialogShow: false,
				drawerShow: false,
				isXy: true, // 协议
				markers: [],
				what: {
					markers: []
				},
				showRead: false,
				detail: {}, // 景区详情数据
				player: {
					id: '', // 播放的音频#
					playerShow: false,
					play: 1, // 1 暂停 2 播放
					img: '',
					spotName: '',
					voiceUrl: '',
					voiceName: ''
				},
				audio: uni.getBackgroundAudioManager(),
				goIndex: false

			}
		},
		async onLoad(option) {

			let yes = await tools.isLogin()
			if (!yes) { // 验证失败
				let register = await tools.logining()
				if (!register) { // 需要注册
					// 设置登录or注册完成后的返回地址
					let pages = getCurrentPages();
					let path = (pages[pages.length - 1].route).slice((pages[pages.length - 1].route).lastIndexOf('/') + 1)
					uni.redirectTo({
						url: '../userInfoAuth/userInfoAuth?goBack=' + path + '&goBackWithParmas=' + JSON.stringify({
							id: this.mapId
						})
					})
				}
			}

			this.mapId = option.id
			if (this.mapId) {
				this.getDetail()
			}


			// 获取 MoneyHide
			this.$store.commit('setMoneyHide', await tools.getMoneyHide())
		},
		onShow() {
			this.what = {
				markers: []
			}
			// 暂时解决锁屏后无法点击mark的问题，但marker无法动态更新（微信问题）会导致叠加，后续版本讲跳转二级页面所有版本迭代可解决
			if (this.mapId) {
				this.getDetail()
			}
			
			let pages = getCurrentPages();
			// 说明只存在
			if (pages.length === 1) {
				this.goIndex = true
			} else {
				this.goIndex = false
			}
		},
		onUnload() {
			this.audio.stop()
			this.player = {
				id: '', // 播放的音频#
				playerShow: false,
				play: 1, // 1 暂停 2 播放
				img: '',
				spotName: '',
				voiceUrl: '',
				voiceName: ''
			}

		},
		onHide() {
			
		},
		methods: {
			mapUpdate(e){
// 				console.log(e)
// 				if (this.mapId) {
// 					this.getDetail()
// 				}
			},
			goToIndex() {
				uni.redirectTo({
					url: '../index/index'
				})
			},
			closeDrawerShow() {
				this.drawerShow = false
			},
			audioPlay() {
				// 暂停操作
				if (this.audio.paused === false) {
					this.audio.stop()
					this.player['play'] = 1
					return
				}

				if (this.audio.paused === undefined || this.audio.paused === true) {
					// 开始播放
					this.audio.src = this.player.voiceUrl;
					this.audio.title = this.player.voiceName;
					this.audio.play()

					this.audio.onPlay(() => {
						this.player['play'] = 2
					});

					this.audio.onError((res) => {
						uni.showToast({
							title: '播放错误',
							icon: 'none'
						});
						this.player['play'] = 1
					});
					this.audio.onEnded((res) => {
						this.player['play'] = 1
					});
				}


			},
			// 关闭播放器
			closePlayer() {
				// 关闭播放器初始化数据及销毁实例
				this.player['playerShow'] = false
				this.audio.stop()
				this.player = {
					id: '', // 播放的音频#
					playerShow: false,
					play: 1,
					img: '',
					spotName: '',
					voiceUrl: '',
					voiceName: ''
				}

			},
			// 阅读协议
			read() {
				this.isXy = true;
				this.showRead = true
			},
			getDetail() {
				let mapContext = uni.createMapContext('map', this);
				http.rq({
					url: allApi.moreSpotList + '/' + this.mapId,
					goBackWithParmas: {
						id: this.mapId
					}
				}).then(res => {
					if (res.code === 200) {
						this.detail = res.data
						this.isUnloack = this.detail.orderId ? 2 : 1;
						let viewArray = []

						res.data.scenicSpotList.forEach(p => {
							this.what.markers.push({
								id: p.id,
								latitude: p.lat,
								longitude: p.lon,
								iconPath: '../../static/image/spot.png',
								width: 40,
								height: 48,
								photo: p.photo,
								spotName: p.spotName,
								label: {
									content: p.spotName,
									anchorX: -((p.spotName.length / 2).toFixed(2) * 16),
									fontSize: 12,
									borderWidth: 1,
									borderColor: '#019ce4',
									padding: 4,
									bgColor: '#fff',
									color: '#019ce4',
									borderRadius: 10,
									textAlign: 'center'
								}
							});
							viewArray.push({
								latitude: p.lat,
								longitude: p.lon
							})
						})
						mapContext.includePoints({
							points: viewArray,
							padding: [40, 40, 40, 40]
						})
					} else {
						this.what.markers = []
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
						this.isUnloack = 1
						this.detail = {}
					}

				})
			},

			showDrawer() {
				this.drawerShow = true
			},

			showPay() {
				this.payDialogShow = true
			},
			closeDrawer() {
				this.payDialogShow = false
			},
			handleScMark({
				mp
			}) {
				// 根据 mp.markerId 获取数据 
				// this.showAudioPop = true
				http.rq({
					url: allApi.voice,
					data: {
						scenicId: this.detail.id,
						spotId: mp.markerId,
						narratorId: this.detail.narratorId
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded', // 默认值
						'token': uni.getStorageSync('login-token')
					},
					goBackWithParmas: {
						id: this.mapId
					},
					loading: true
				}).then(res => {
					if (res.code === 200) {
						if (!res.data) {
							uni.showToast({
								title: '没有语音',
								icon: 'none'
							});
							return
						}

						this.player['id'] = res.data.id;
						this.player['voiceUrl'] = res.data.voiceUrl;
						this.player['voiceName'] = res.data.voiceName;

						this.what.markers.forEach(a => {
							if (a.id === mp.markerId) {
								this.player['img'] = a.photo;
								this.player['spotName'] = a.spotName;
							}
						})

						this.player['playerShow'] = true

					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				})
			},
			agree(type) {
				if (type == 'yes') {
					this.isXy = true
				} else {
					this.isXy = false
				}
				this.showRead = false
			},
			mackPay() {
				if (!this.$store.state.MoneyHide) {
					return
				}

				if (!this.isXy) {
					uni.showToast({
						title: '请阅读协议并同意',
						icon: 'none'
					})
					return
				}

				// 獲取後台訂單數據
				this.formSubmit()

			},
			formSubmit(e) {
				let _this = this
				let par = {
					narratorId: this.detail.narratorId,
					scenicId: this.detail.id,
				}
				uni.showLoading({
					title: '生成订单中'
				});

				http.rq({
					url: allApi.createOrder,
					method: 'POST',
					data: par,
					goBackWithParmas: {
						id: this.mapId
					}
				}).then(res => {
					uni.hideLoading();
					if (res.code === 200) {
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: res.data.timeStamp,
							nonceStr: res.data.nonceStr,
							package: res.data.packageValue,
							signType: res.data.signType,
							paySign: res.data.paySign,
							success: function(res) {
								uni.showToast({
									title: '支付成功',
									icon: 'none'
								})
								_this.payDialogShow = false
								_this.getDetail()
							},
							fail: function(err) {}
						});

					} else {
						uni.showToast({
							title: '生成订单失败',
							icon: 'none'
						})
					}

				})


			},

		},
		watch: {
			payDialogShow(v) {
				if (v === true) {
					this.isXy = true
				}
			},

		},
		computed: {
			...mapState(['MoneyHide'])
		}
	}
